/**
  * 通用字体族（去掉部分不用的）
  * https://developer.mozilla.org/en-US/docs/Web/CSS/font-family#%3Cgeneric-name%3E
*/
type GenericName = 'serif' | 'sans-serif' | 'cursive' | 'fangsong' | 'fantasy' | 'monospace' | 'system-ui' | 'emoji' | 'math';

/**
 * 字重
 * https://developer.mozilla.org/en-US/docs/Web/CSS/font-weight#Common_weight_name_mapping
 * ```
 * 100 | 200 | 300 | 400 | 500 | 600 | 700 | 800 | 900 | 950
 * ```
 */
type BaseFontWeight = 'Thin' | 'ExtraLight' | 'Light' | 'Normal' | 'Medium' | 'Semibold' | 'Bold' | 'ExtraBold' | 'Black' | 'ExtraBlack';
type FontWeight = BaseFontWeight | 'Ultralight' | 'UltraBold' | 'Regular' | 'W3' | 'W6' | 'Heavy';

/**
 * 字符集（部分来源于统计）
 */
type CharSet = 'GBK' | 'GBK_M' | 'GB2312' | 'Unicode';

export interface FontsInfo {
  /**
   * 字体名
   * 例如 Songti SC Light、Songti SC Regular、Songti SC Black、Songti SC Bold 的 family 都是 Songti SC
   */
  family: string;
  /**
   * 中文名
   * 例如 Songti SC 是 宋体-简
   */
  generic: GenericName;
  chinesefamily?: string;
  postScriptName?: string;
  weights?: FontWeight[];
  charSets?: CharSet[];
  fallback?: string[];
  /**
   * baseline-to-baseline-distance，用于行距测量
   */
  btbd?: number;
}

